import axios from "axios";
import { createContext, useEffect, useState } from "react"

export const APIContext= createContext()
export default function APIContextFunc(props){
  let storedParam;
  const [games, setGames] = useState([]);
  const [detect, setDetect] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  let [num, setNum] = useState(20)
  const [more, setMore] = useState(true)
  useEffect(() => {
    getData()
  },[])
  function noMore(){
    setMore(false)
    console.log(more)
  }

  async function getData(param) {
    if(param !== undefined){
      localStorage.setItem("data" , JSON.stringify(param))
      storedParam = param
    } else {
      storedParam = JSON.parse(localStorage.getItem("data"))
    }
    let options = {
   params: storedParam,
   headers: {
     'X-RapidAPI-Key': '4b6898047dmsh1ae70660b50e013p1019bdjsnbda44bb4a578',
     'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
   }
  };
    const { data } = await axios.get('https://free-to-play-games-database.p.rapidapi.com/api/games', options)
    setGames(data.slice(0, num));
    setDetect(data)
    setIsLoading(false)
   
  }
  function addMore() {
    setNum(num += 20)
    if (detect.length <= num) {
      setMore(false)
      noMore()
      console.log("detect")
    } else
    setMore(true)
    getData()
  }
  return <APIContext.Provider value={{ games: games, addMore: addMore, getData: getData, isLoading: isLoading, more :more}}>
  {props.children}
  </APIContext.Provider>
}