import React, { useContext } from 'react'
import {  Link,NavLink } from 'react-router-dom'
import { APIContext } from '../../APIContext/APIContext'


export default function Navbar({data ,logOut}) {
  let {getData}=useContext(APIContext)
  return <>
    <nav className="navbar navbar-expand-lg shadow navbar-dark">
  <div className="container">
    <Link className= "logo navbar-brand" to="/"><img src={require("../../img/logo.png")} className="logoImg" alt="logo" />Game Over</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse ms-4" id="navbarSupportedContent">
          {data ? <ul className="navbar-nav  me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink className="nav-link " aria-current="page" to="/">Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" onClick={() => getData(null)} to="all">All</NavLink>
            </li>
            <li className="nav-item dropdown">
              <NavLink className="nav-link dropdown-toggle" to="platform" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Platforms
              </NavLink>
              <ul className="dropdown-menu">
                <li ><Link to="platform/pc" className="dropdown-item" onClick={() => getData( { platform: "pc"} )} >PC</Link></li>
                <li ><Link to="platform/browser" className="dropdown-item" onClick={() => getData({ platform: "browser" })} >Browser</Link></li>
              </ul>
            </li>
            <li className="nav-item dropdown">
              <NavLink className="nav-link dropdown-toggle" to="sortby" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Sor tby
              </NavLink>
              <ul className="dropdown-menu">
                <li ><Link to="sortby/release-date" onClick={() => getData({ "sort-by": "release-date"} )}  className="dropdown-item" >release-date</Link></li>
                <li ><Link to="sortby/popularity" onClick={() => getData({ "sort-by": "popularity"} )} className="dropdown-item" >popularity</Link></li>
                <li ><Link to="sortby/alphabetical" onClick={() => getData({ "sort-by": "alphabetical"} )} className="dropdown-item" >alphabetical</Link></li>
                <li ><Link to="sortby/relevance" onClick={() => getData({ "sort-by": "relevance"} )} className="dropdown-item" >relevance</Link></li>
              </ul>
            </li>
            <li className="nav-item dropdown">
              <NavLink className="nav-link dropdown-toggle" to="categories" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Categories
              </NavLink>
              <ul className="dropdown-menu">
                <li><Link to="category/racing" onClick={() => getData({ "category": "racing" })} className="dropdown-item">Racing</Link></li>
                <li> <Link to="category/social" onClick={() => getData({ "category": "social"} )} className="dropdown-item"> Social</Link></li>
                <li> <Link to="category/shooter" onClick={() => getData({ "category": "shooter"} )} className="dropdown-item">Shooter</Link></li>
                <li> <Link to="category/open-world" onClick={() => getData({ "category": "open-world"} )} className="dropdown-item">Open-world </Link></li>
                <li> <Link to="category/zombie" onClick={() => getData({ "category": "zombie"} )} className="dropdown-item">Zombie </Link></li>
                <li> <Link to="category/fantasy"onClick={() => getData({ "category": "fantasy"} )} className="dropdown-item" >Fantasy </Link></li>
                <li> <Link to="category/action-rpg" onClick={() => getData({ "category": "action-rpg"} )} className="dropdown-item">Action-rpg </Link></li>
                <li> <Link to="category/action" onClick={() => getData({ "category": "action"} )} className="dropdown-item">Action  </Link></li>
                <li> <Link to="category/battle-royale" onClick={() => getData({ "category": "battle-royale" })} className="dropdown-item"> Battle-royale </Link></li>
                <li> <Link to="category/sport"onClick={() => getData({ "category": "sport"} )} className="dropdown-item">Sport  </Link></li>
              </ul>
            </li>
          </ul> : ""}
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
            {data ? <li className="nav-item">
          <span className="nav-link ms-2 px-3 py-2 sign" onClick={logOut } >Log Out</span>
        </li> : <>
                <li className="nav-item">
                  <NavLink className="nav-link" aria-current="page" to="login">Login</NavLink>
                </li>
                <li className="nav-item">
                  <NavLink className="nav-link ms-2 px-3 py-2 sign" to="Registration">Join Free</NavLink>
                </li>
        </>}
        </ul>
    </div>
  </div>
</nav>

  </>
}
