import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import styles from "./GameDetails.module.css"
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import { Helmet } from 'react-helmet';
import { data } from 'jquery';
export default function GameDetails() {
  const { id } = useParams()
  const [details, setDetails] = useState([]);
  const options = {
    params: { id: `${id}` },
    headers: {
      'X-RapidAPI-Key': '4b6898047dmsh1ae70660b50e013p1019bdjsnbda44bb4a578',
      'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
    }
  };
  useEffect(() => {
    getDetails()
  }, [])

  async function getDetails() {
    const { data } = await axios.get('https://free-to-play-games-database.p.rapidapi.com/api/game', options)
    setDetails(data);
  }
  return <>
    <div className="application">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Game Details</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
    </div>
    <div className="container">
      <div className="row mt-4">
        <div className="col-md-4">
          <div>
            <figure>
              <img src={details.thumbnail} className="w-100" alt="game pic" />
            </figure>
            <div className='d-flex justify-content-between align-items-center'>
              <span className={styles.freebadge + ' badge py-3 px-3 fs-6 fw-light  p-2 '}>Free</span>
              <a className={"btn d-block w-75 py-2 " + styles.btncolord} href={details.freetogame_profile_url} target="_blank">
                <div className='d-flex align-items-center justify-content-center '>
                  <h5 className='fw-bold text-white'>Play Now</h5>
                  <i className='fas fa-sign-out-alt text-white ms-1'></i>
                </div>
              </a>
            </div>
          </div>
        </div>
        <div className="col-md-8">
          <div>
            <div className='d-flex justify-content-between align-items-center'>
              <h2  >{details.title}</h2>
              <div>
                <span className='badge text-white bg-info me-3'>{details.genre}</span>
                {details.status === "Live" ? <span className='badge text-white bg-danger'>{details.status}</span> : <span className='badge text-white bg-secondary'>{details.status}</span>}
              </div>
            </div>
            <h5>About {details.title}</h5>
            <p>{details.description}</p>
            <div>
              {details.minimum_system_requirements !== undefined ? <div>
                <h3>Minimum System Requirements</h3>
                <ul className='list-unstyled'>
                  <li> <span className='fw-bold'>OS:</span> {details.minimum_system_requirements.os} </li>
                  <li> <span className='fw-bold'>Processor:</span> {details.minimum_system_requirements.processor} </li>
                  <li> <span className='fw-bold'>Graphics:</span> {details.minimum_system_requirements.graphics} </li>
                  <li> <span className='fw-bold'>Memory:</span> {details.minimum_system_requirements.memory} </li>
                  <li> <span className='fw-bold'>Storage:</span> {details.minimum_system_requirements.storage} </li> </ul></div> : ""}
              <div>
                {details.screenshots !== undefined ? <div>
                  <h2>{details.title} Screenshots</h2>
                  <OwlCarousel items={1} loop  autoplay={true} >
                    {details.screenshots.map((screen, index) => <figure className='item' key={index}><img src={screen.image} className="w-100" alt="" /></figure>)}
                  </OwlCarousel>
                </div> : ""}
              </div>
              <h2>Additional Information</h2>
              <hr />
              <div className="row">
                <div className="col-6 col-md-4">
                  <div>
                    <samall>Title</samall>
                    <p className={styles.para + " fw-semibold"}>{details.title}</p>
                  </div>
                </div>
                <div className="col-6 col-md-4">
                  <div>
                    <samall>Developer</samall>
                    <p className={styles.para + " fw-semibold"}>{details.developer}</p>
                  </div>
                </div>
                <div className="col-6 col-md-4">
                  <div>
                    <samall>Publisher</samall>
                    <p className={styles.para + " fw-semibold"}>{details.publisher}</p>
                  </div>
                </div>
                <div className="col-6 col-md-4">
                  <div>
                    <samall>Release Date</samall>
                    <p className={styles.para + " fw-semibold"}>{details.release_date}</p>
                  </div>
                </div>
                <div className="col-6 col-md-4">
                  <div>
                    <samall>Platform</samall>
                    <p className={styles.para + " fw-semibold"}>{details.platform}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
}
