import {
  createHashRouter,
  Navigate,
  RouterProvider,
} from "react-router-dom";
import './App.css';
import Home from "./Components/Home/Home";
import Main from "./Components/Main/Main";
import Platform from "./Components/Platforms/Platform";
import SortBy from "./Components/Sortby/SortBy";
import Categories from "./Components/Categories/Categories";
import Login from "./Components/Login/Login";
import Registration from "./Components/Registration/Registration";
import All from "./Components/All/All";
import GameDetails from "./Components/GameDetails/GameDetails";
import jwtDecode from 'jwt-decode'
import { useEffect, useState } from "react";
import NotFound from "./Components/NotFound/NotFound";

function App() {
  
  useEffect(()=>{
    if (localStorage.getItem("token")) {
      getUserData()
    }
  },[])
  const [userData, setUserData] = useState(null)
  function getUserData(){
    let token = localStorage.getItem("token")
    if (token){
      let data = jwtDecode(token)
      setUserData(data)
    }
  }
  function logOut() {
    localStorage.removeItem("token")
    setUserData(null);
    <Navigate to="/login"></Navigate>
  }
  function Protect(props){
    if(localStorage.getItem("token") === null)
      return <Navigate to="/login"/>
    else 
    return props.children
  }
  const router = createHashRouter([
    {
      path: "/",
      element: <Main data={userData} logOut = { logOut } />,
      children: [
        {
          path: "/" ,
          element: <Protect><Home /></Protect>
        },
        {
          path: "all",
          element: <Protect><All/></Protect>
        },
        {
          path: "platform/:id",
          element: <Protect><Platform/></Protect>
        },
        {
          path: "sortby/:id",
          element:<Protect> < SortBy/></Protect>
        },
        {
          path: "category/:id",
          element: <Protect><Categories/></Protect>
        },
        {
          path: "gameDetails/:id",
          element: <Protect><GameDetails/></Protect>
        },
        {
          path: "Registration",
          element: <Registration/>
        },
        {
          path: "login",
          element: <Login logVer={getUserData} />
        }, 
        {
          path:"*", 
          element: <NotFound></NotFound>
        }
      ],
    },
  ]);
  return <>
   <RouterProvider router={router} />
</>
}

export default App;
