import React, { useContext } from 'react';
import { Helmet } from 'react-helmet';
import { Link } from 'react-router-dom';
import { APIContext } from '../../APIContext/APIContext';
import Loading from '../Loading/Loading';
import styles from "./Platform.module.css"
export default function Platform() {

  const {addMore , games, isLoading , more}= useContext(APIContext)
  return <>
    <div className="application">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Platform</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
    </div>
    {isLoading && <Loading></Loading>}
    {!isLoading && <div className="container p-0" >
      <div className="row my-4 gy-4">
        {games.map((game, index) => <Link to={"/gameDetails/" + game.id} className="text-decoration-none col-lg-3 col-md-4" key={index}>
          <div>
            <div className={styles.card + " card border-0 "}  >
              <img src={game.thumbnail} className="w-100  " alt="game pic" />
              <div className={" card-body rounded-bottom"}>
                <div className="d-flex justify-content-between align-items-center">
                  <h5 className="card-title fw-semibold">{game.title.length >= 13 ? game.title.substring(0, 11) + "..." : game.title} </h5>
                  <span className={styles.bgcolored1 + " badge  text-white"}>Free</span>
                </div>
                <small>{game.short_description.substring(0, 23)}...</small>
                <div className='d-flex justify-content-between align-items-center mt-2'>
                  <i className='fas fa-plus-square'></i>
                  <div>
                    <small className={styles.bgcolored2 + " badge px-2  me-2 "}>{game.genre}</small>
                    <span>
                      {game.platform !== 'Web Browser' ? <i className='fab fa-windows text-muted'></i> : <i className='fas fa-window-maximize text-muted '></i>}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Link>)}
      </div>
      {more === false ? <div className='alert alert-secondary'>There is no more games :(</div> : ""}
      <button onClick={addMore} className={styles.hovring + ' d-block mx-auto btn btn-outline-secondary my-4 mb-5'}>More Games <i className='fas fa-chevron-right '></i></button>
    </div>}
  </>
}
