import React, { useEffect, useState } from 'react'
import styles from "./Registration.module.css"
import { Link, useNavigate } from 'react-router-dom'
import Background from "../../img/gaming.jpg"
import  axios  from 'axios'
import Joi from 'joi'
import { Helmet } from 'react-helmet'
export default function Registration() {
  const [user, setUser] = useState({
"first_name":"",
"last_name":"",
"email":"",
"password":"",
"age":""
})
  const [btnFlag, setBtnFlag] = useState(false)

  const [joiErr, setJoiErr] = useState(null)
  
  const [APIMessage, setAPIMessage] = useState("")
  const navigate = useNavigate()
  function getUser(e) {
    setAPIMessage("")
    setJoiErr(null)
    let newUser = {...user}
    let id = e.target.id
    newUser[id] = e.target.value
    setUser(newUser)
  }
  const Joi = require('joi');

const schema = Joi.object({
    first_name: Joi.string()
        .alphanum()
        .min(3)
        .max(30)
        .required()
        .messages(
          {
            "string.empty": "First Name cannot be empty",
            "string.min": "Your name must be more than 3 char like 'Game'",
            "string.max": "Your name must be less than 30 char like 'Game Over'"
          }
        ),
    last_name: Joi.string()
        .alphanum()
        .min(3)
        .max(30)
        .required()
        .messages(
          {
            "string.empty": "First Name cannot be empty",
            "string.min": "Your name must be more than 3 char like 'Game'",
            "string.max": "Your name must be less than 30 char like 'Game Over'"
          }
        ),
        email: Joi.string()
        .email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } })
        .messages(
        {
          "string.empty": "Email cannot be empty",
          "string.pattern.base": "You must write a valid email 'gameover@example.com'"
        }
      ).required(),
        age: Joi.number()
        .min(18)
        .max(60)
        .required()
      .messages(
          {
            "number.empty": "age cannot be empty",
            "number.min": "Sorry your age must be greater than or equal to 18",
            "number.max": "Sorry your age must be smaller than or equal to 18",
          }
          ),
      password: Joi.string()
          .pattern(new RegExp('^[a-zA-Z0-9]{3,15}$'))
          .messages(
            {
              "string.empty": "Passowrd cannot be empty",
              "string.pattern.base": "You must write a valid Password like 'gameover123'"
            }
          )
})
let validation =schema.validate(user, {abortEarly: false})

  function postUser(e){
    setBtnFlag(true)
  e.preventDefault()
  if(validation.error === undefined){
      sendUser()
  } else {
    setJoiErr(validation.error.details)
    console.log(validation.error)
    setAPIMessage("")
    setBtnFlag(false)
  }
}
 async function sendUser(){
   let { data } = await axios.post("https://sticky-note-fe.vercel.app/signup", user)
  if(data.message === "success"){
    navigate("/login")
  } else {
    setAPIMessage("Email is already exist")
    setJoiErr([])
  }
   setBtnFlag(false)
}
  function displayChecking(key) {
    if (joiErr !== null) {
      for (let i = 0; i < joiErr.length; i++) {
        if (joiErr[i].context.key === key) {
          return joiErr[i].message;
        } else return ""
      }
    }
  }
  return <>
    <div className="application">
      <Helmet>
        <meta charSet="utf-8" />
        <title>Registration</title>
        <link rel="canonical" href="http://mysite.com/example" />
      </Helmet>
    </div>
        <div className="container">
      <div className="row gx-0">
        <div className={`col-lg-6 d-lg-block d-none ${styles.gaming}`} style={{ backgroundImage: `url(${Background})` }} >
        </div>
        <div className={`${styles.login} col-lg-6`} >
          <div>
            <form onSubmit={postUser}>
              <div className="row px-3">
                <div className="col-12 my-3">
                    <div className='text-center'>
                      <h3>Create My Account!</h3>
                    </div>
                </div>
                  <div className="col-md-6 mb-3">
                    <div>
                      <input type="text" onChange={getUser} className='form-control' id='first_name' placeholder='Frist Name' />
                    </div>
                  {displayChecking("first_name") ? <div className={styles.alertred + " alert d-flex justify-content-between align-items-center px-3 "} > {displayChecking("first_name")} <i  className=" fa-solid fa-triangle-exclamation" ></i> </div> : ""} 
                  </div>
                  <div className="col-md-6 mb-3">
                    <div>
                      <input type="text" onChange={getUser} className='form-control' id='last_name' placeholder='Last Name ' />
                    </div>
                  {displayChecking("last_name") ? <div className={styles.alertred + " alert d-flex justify-content-between align-items-center px-3 "} > {displayChecking("last_name")} <i  className=" fa-solid fa-triangle-exclamation" ></i> </div> : ""} 

                  </div>
                  <div className="col-12 mb-3">
                    <div>
                      <input type="text" onChange={getUser} className='form-control' id='email' placeholder='Email Address' />
                    </div>
                  {displayChecking("email") ? <div className={styles.alertred + " alert d-flex justify-content-between align-items-center px-3 "} > {displayChecking("email")} <i className=" fa-solid fa-triangle-exclamation" ></i> </div> : ""} 

                  </div>
                  <div className="col-12 mb-3">
                    <div>
                      <input type="text" onChange={getUser} className='form-control' id='age' placeholder='Age' />
                    </div>
                  {displayChecking("age") ? <div className={styles.alertred + " alert d-flex justify-content-between align-items-center px-3 "} > {displayChecking("age")} <i className=" fa-solid fa-triangle-exclamation" ></i> </div> : ""} 

                  </div>
                  <div className="col-12 mb-2">
                    <div>
                      <input type="password" onChange={getUser} className='form-control' id='password' placeholder='Password' />
                    </div>
                  {displayChecking("password") ? <div className={styles.alertred + " alert d-flex justify-content-between align-items-center px-3 "} > {displayChecking("password")} <i className=" fa-solid fa-triangle-exclamation" ></i> </div> : ""} 

                  </div>
                  {APIMessage !== "" ? <div className={styles.alertred + " alert d-flex justify-content-between align-items-center px-3"} >{APIMessage} <i class="fa-solid fa-triangle-exclamation"></i></div> : "" }
                  <div className="col-12 my-2">
                    <div>
                      <button className= {`${styles.logbtn}  btn text-white  w-100 d-block`}>
                      {btnFlag ? <i class="fa-solid fa-spinner fa-spin"></i> : "Sign Up"}
                      </button>
                    </div>
                  </div>
                  <div className={`${styles.border} col-12 `}>
                    <div>
                      <small className='mb-2 d-block'>This site is protected by reCAPTCHA and the Google <a href="https://policies.google.com/privacy" target="_blank">Privacy Policy</a> and <a href="https://policies.google.com/terms" targrt="_blank">Terms of Service</a> apply.</small>
                    </div>
                  </div>
                  <div className="col-12 text-center  mt-3 mb-4">
                    <div>
                      <small className={styles.already } >Already a member? <Link className={` ${styles.signin} + text-decoration-none`} to="/login"> Log In <i className='fas fa-chevron-right small'></i></Link></small>
                    </div>
                  </div>
                </div> 
            </form>
          </div>
        </div>
      </div>
    </div>
  </>
}
