import React from 'react'
import { Helmet } from 'react-helmet'

export default function NotFound() {
  return <><div className="application">
          <Helmet>
            <meta charSet="utf-8" />
            <title>Home</title>
            <link rel="canonical" href="http://mysite.com/example" />
          </Helmet>
        </div>
        <div className='d-flex justify-content-center align-items-center flex-column vh-100 view'>
          <h1>Not found :(</h1>
          <p>There is no page whith this path.</p>
        </div>
      </>

}
